import React from "react"
import PropTypes from "prop-types"
import { Link } from "gatsby"
import { useStaticQuery, graphql } from "gatsby"
import { FaGithub } from "react-icons/fa"
import Header from "./header"
import "./layout.css"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <Header siteTitle={data.site.siteMetadata.title} />
      <main className="show-footer">{children}</main>
      <footer className="footer-text text-center text-gray-700 text-xs m-2 pb-4">
        <Link className="hyperlink" to="/portfolio-project/contact/">
          Get in touch.
        </Link>{" "}
        <br />
        <br />
        <a
          className="flex hyperlink m-auto text-2xl pb-1 w-4"
          href="https://github.com/tim-corley/portfolio-project"
        >
          <FaGithub />
        </a>
        © {new Date().getFullYear()} TIM CORLEY
        <div>
          BUILT WITH
          {` `}
          <a
            className="hyperlink text-gray-700 font-bold"
            href="https://www.gatsbyjs.org"
          >
            GATSBY.JS
          </a>
        </div>
      </footer>
      {/* </div> */}
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
